body {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #ff3d00 #ff3d00;
  border-radius: 50%;
  margin-top: -24px;
  margin-left: -24px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  transform-origin: center;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #ff3d00 #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:before {
  width: 32px;
  height: 32px;
  border-color: #63ccca #63ccca rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation: rotation 1.5s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.hidden {
  display: none;
}

span {
  font-weight: 600;
}

.breed-select {
  margin-bottom: 20px;
}

.cat-info {
  flex-wrap: wrap;
  display: flex;
}

.info-wrapper {
  max-width: 600px;
}

.cat-img {
  border-radius: 20px;
  margin-right: 20px;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, .2);
}

