.container {
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  margin: 0 auto;
}

.hidden {
  display: none;
}

span {
  font-weight: 600;
}

.breed-select {
  margin-bottom: 20px;
}

.cat-info {
  display: flex;
  flex-wrap: wrap;
}

.info-wrapper {
  max-width: 600px;
}

.cat-img {
  margin-right: 20px;
  border-radius: 20px;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}
